





























import mixpanel from 'mixpanel-browser'
import Vue from 'vue'

export default Vue.extend({
  data() {
    return {
      active: false,
    }
  },
  props: {
    image: {
      type: String,
      required: true,
    },
    text: {
      type: String,
      required: true,
    },
    href: {
      type: String,
      required: true,
    },
  },
  methods: {
    onClick(href: string) {
      mixpanel.track('Icon-click', {
        to: href,
      })
    },
    onVisible(visible: boolean) {
      this.active = visible

      if (visible) setTimeout(() => this.active = false, 1000)
    },
  },
})
