






































import Vue from 'vue'

export default Vue.extend({
  model: {
    event: 'model',
  },
  props: {
    label: {
      required: true,
      type: String,
    },
  },
  data() {
    return {
      focus: false,
    }
  },
  computed: {
    disabled(): boolean {
      return this.$attrs.disabled !== undefined &&
        // eslint-disable-next-line
        // @ts-ignore
        this.$attrs.disabled !== false
    },
  },
  methods: {
    onBlur() {
      this.focus = false
    },
    onFocus() {
      this.focus = true
    },
    onModel({ value }: HTMLInputElement | HTMLTextAreaElement) {
      this.$emit('model', value)
    },
  },
})
