





























































import mixpanel from 'mixpanel-browser'
import Vue from 'vue'

export default Vue.extend({
  data() {
    return {
      active: false,
      visible: false,
    }
  },
  props: {
    icon: {
      required: true,
      type: String,
    },
    timeEvents: {
      type: Array as () => TimeEvent[],
      required: true,
    },
    title: {
      required: true,
      type: String,
    },
  },
  methods: {
    onEventClick(title: string) {
      mixpanel.track('Event-click', {
        event: title,
      })
    },
    onVisible(visible: boolean) {
      this.visible = visible
    },
  },
})
