











import Vue from 'vue'
import helper, { captchaCallback } from '@/assets/helpers'

export default Vue.extend({
  model: {
    event: 'captcha',
  },
  props: {
    callback: {
      required: false,
      type: Function,
    },
    value: {
      required: true,
      type: String,
    },
  },
  data() {
    return {
      captcha: {},
      model: this.value,
    }
  },
  watch: {
    value() {
      this.model = this.value
    },
  },
  mounted() {
    const callback = this.callback as captchaCallback
    this.captcha = helper.captcha('.captcha .input-input', callback)
  },
})
