












import Vue from 'vue'

export default Vue.extend({
  model: {
    event: 'change',
  },
  props: {
    value: {
      type: Boolean,
      required: false,
    },
  },
  data() {
    return {
      checked: false,
    }
  },
  watch: {
    checked() {
      this.$emit('change', this.checked)
    },
    value() {
      this.checked = this.value
    },
  },
})
