







































import Vue from 'vue'
import Locale from '@/components/Locale.vue'
import MenuButton from './MenuButton.vue'
import mixpanel from 'mixpanel-browser'

export default Vue.extend({
  components: {
    Locale,
    MenuButton,
  },
  data() {
    return {
      background: false,
      open: false,
    }
  },
  computed: {
    heigth(): number {
      return 60
    },
    links(): Array<Link> {
      return [
        {
          text: 'menu.about',
          class: 'profile',
        },
        {
          text: 'menu.programming',
          class: 'programming',
        },
        {
          text: 'menu.experience',
          class: 'experience',
        },
        {
          text: 'title.contact',
          class: 'contact',
        },
      ]
    },
  },
  methods: {
    onNav(toClass: string) {
      const el = document.querySelector(`.${toClass}`) as HTMLElement
      const { top } = el.getBoundingClientRect()

      window.scrollTo({
        top: window.scrollY + top - 60,
      })

      this.open = false

      mixpanel.track('Menu-click', {
        to: toClass,
      })
    },
    onScroll() {
      this.background = window.scrollY > this.heigth + 1
    },
  },
  mounted() {
    window.addEventListener('scroll', this.onScroll)
  },
  beforeDestroy() {
    window.removeEventListener('scroll', this.onScroll)
  },
})
