



































import Vue from 'vue'

export default Vue.extend({
  model: {
    event: 'show',
  },
  props: {
    message: {
      required: true,
      type: String,
    },
    title: {
      required: true,
      type: String,
    },
    type: {
      required: true,
      type: String as () => 'error' | 'success' | 'info',
    },
    value: {
      required: true,
      type: Boolean,
    },
  },
  methods: {
    close() {
      this.$emit('show', false)
    },
  },
})
